@import "../../utils/media.scss";

.footer {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;

  &Container {
    width: 100%;
    padding: 6rem 4rem;

    @include respond(phone) {
      padding: 6rem 0;
      padding-left: 2rem;
    }

    &Brands {
      width: 100%;
      display: flex;
      justify-content: flex-start;
      align-items: flex-end;

      img {
        margin-right: 3rem;
      }

      svg {
        margin-right: 3.3rem;
        cursor: pointer;

        * {
          transition: all 100ms ease-in-out;
        }

        &:hover {
          * {
            fill: #ffc404;
          }
        }
      }
    }

    &Info {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 2.5rem;

      @include respond(phone) {
        flex-direction: column;
      }

      &__links {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-end;
        margin-right: auto;

        a {
          color: #ffffff;
          font-family: "Montserrat", sans-serif;
          font-size: 1.2rem;
          font-weight: 500;
          letter-spacing: 1.01px;
          line-height: 19.67px;
          text-align: left;
          text-decoration: none;
          margin-bottom: 1.5rem;

          &:last-of-type {
            margin-bottom: none;
          }
        }
      }

      &__createBy {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
        margin-left: auto;

        @include respond(phone) {
          margin: 0;
          justify-content: center;
          align-items: flex-start;
          margin-top: 26px;
          margin-right: auto;
        }

        p {
          margin-top: 2rem;
          color: #ffffff;
          font-family: "Montserrat", sans-serif;
          font-size: 1.4rem;
          font-weight: 700;
          line-height: 17px;
          text-align: left;

          span {
            font-weight: 100;
          }
        }
      }
    }
  }
}