@import "../../utils/media.scss";
@import "../../utils/animations.scss";
.contentLanding {
  width: 100%;
  min-height: 100vh;
  position: relative;
  padding-top: 100px;
  .loading {
    width: 100%;
    height: 60vh;
  }
  &Serie {
    position: relative;
    width: 100%;
  }
  .video {
    &Container {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 6000;
      width: 100%;
      height: 100vh;
      background-color: black;
      &Close {
        position: absolute;
        top: 50px;
        right: 50px;
        z-index: 7000;
        cursor: pointer;
      }
      div {
        width: 100% !important;
        height: 100% !important;
      }
    }
    width: 100%;
  }
}

.carousel {
  &Background {
    opacity: 0.1;
    width: 100%;
    height: 100%;

    position: fixed;
    top: 0;
    left: 0;
    z-index: 0;
    height: 100%;
    max-height: 659px;
    animation: fadeInLanding 500ms ease-in-out;
    animation-delay: 500ms;
    animation-fill-mode: forwards;
    z-index: -1;
    &.fromLanding {
      height: 659px;
      // animation: translateFromRightWithOpacity 500ms ease-in-out;
      // animation-delay: 300ms;
      // animation-fill-mode: backwards;
    }
    @include respond(phone) {
      height: 200px;
    }
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      max-width: 100%;
      position: absolute;
      top: 0;
      right: 0;
      z-index: -1;
      transition: all 300ms ease-in-out;
    }
  }

  // &LeftGradient {
  //   width: 70%;
  //   height: 100%;
  //   position: absolute;
  //   top: 0;
  //   left: 0;
  //   // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
  //   background: linear-gradient(to right, rgba(17, 17, 30, 1) 0%, rgba(17, 17, 30, 0.8) 50%, rgba(17, 17, 30, 0.6951155462184874) 70%, rgba(17, 17, 30, 0) 100%);
  //   z-index: 0;
  //   @include respond(phone) {
  //     display: none;
  //   }
  // }
  &BottomGradient {
    width: 100%;
    height: 82px;
    position: absolute;
    left: 0;
    bottom: 0;
    // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
    background: linear-gradient(to top, rgba(17, 17, 30, 0.99) 0%, rgba(0, 0, 0, 0) 100%);
    z-index: 0;
  }
}
