.nextEpisode {
  width: fit-content;
  position: fixed;
  bottom: 75px;
  right: 30px;
  // background-color: rgba(#11111e, 0.5);
  z-index: 100000;
  transition: all 500ms ease-in-out;
  opacity: 0;
  visibility: hidden;
  transform: translateY(500px);
  padding-bottom: 10px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  padding: 10px;
  background-color: rgba(#11111e, 1);
  border-radius: 12.38px;

  &.show {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }

  &Buttons {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    z-index: 100;
    padding: 1rem;
  }

  .remaining {
    font-family: "Montserrat", sans-serif;
    font-size: 20px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: -0.06px;
    color: #f5f5f5;
    margin-bottom: 6px;
    span {
      color: #ffc404;
    }
  }

  &Button {
    background-color: #6432d8;
    border: 1.38px solid transparent;
    border-radius: 12.38px;
    padding: 6px 0;
    color: #fefefe;
    font-family: "Montserrat", sans-serif;
    font-size: 1rem;
    font-weight: 600;
    outline: none;

    cursor: pointer;
    width: 33%;
    &.toPurchase {
      background-color: #ffc404;
      border: 1.38px solid #ffc404;
      color: #211f35;
    }
  }

  &_caratula {
    width: 364px;
    height: 205px;
    border-radius: 10px;
    background-color: #d8d8d8;
    z-index: 100;
    border: 1.38px solid #ffc404;
    margin-right: 36px;
    img {
      border-radius: 10px;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}
