@import "../../../utils/media.scss";
@import "../../../utils/animations.scss";

.buttonModalAlert {
  position: relative;
  display: block;
  width: 100%;
  height: 40px;
  background-color: #ffc404;
  // border: 2px solid transparent;
  border: none;
  outline: none;
  color: #000;
  font-family: "Montserrat", sans-serif;
  font-size: 1.4rem;
  font-weight: bold;
  letter-spacing: -0.19px;
  border-radius: 56px;
  text-transform: uppercase;
  cursor: pointer;
  position: relative;
  max-width: 247px;
  z-index: 100;
  transition: all 200ms ease-in-out;

  @include respond(phone) {
    max-width: 100%;
    margin: 30px 0;
    margin-bottom: 0;
  }

  &.fromLanding {
    @include respond(phone) {
      margin: 20px 0;
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    width: 15.14px;
    height: 15.98px;
    background: url("../../../assets/play-button.svg") center / cover no-repeat;
  }

  &:hover {
    // border-color: #fff !important;
    transform: scale(1.1);
  }

  &.fromNextEpisode {
    // margin-left: 58px;
    height: 36px;
    max-height: 36px;
    font-size: 10.8px;
    max-width: 223px;

    &:hover {
      transform: none;
    }
  }

  &.changeStatus {
    background-color: #6432d8;
    color: #fff;

    &::before {
      content: none;
    }
  }

  &.secondary {
    &:hover {
      border-color: transparent !important;
    }
  }

  &.omitir {
    background-color: #535050;
    color: white;

    &::before {
      content: none;
    }
  }
}

.lottie {
  @include respond(phone) {
    max-width: 100%;
    margin: 30px 0;
  }
}

.svg {
  width: 247px;
  transition: all 300ms ease-in-out;
  cursor: pointer;
  // animation: translateFromBottomWithOpacity 500ms ease-in-out;
  // animation-delay: 1500ms;
  // animation-fill-mode: backwards;
  z-index: 100000;

  &:hover {
    transform: scale(1.1);
  }
}
