.select {
  width: 100%;
  position: relative;
  margin-bottom: 20px;
  &Main {
    background-color: #ffffff;
    border-radius: 20.5px;
    box-shadow: 0 2px 18px 0 rgba(0, 0, 0, 0.2);
    border: none;
    color: #707070;
    font-size: 13.2px;
    font-weight: 300;
    text-align: left;
    padding: 10px 0 10px 70px;
    width: 100%;
    outline: none;
    position: relative;
    cursor: pointer;
    z-index: 1001;
    span {
      font-family: "Montserrat", sans-serif;
    }
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 5px;
      transform: translateY(-50%);
      width: 32px;
      height: 32px;
      background: url("../../../assets/country-code.svg") center/cover no-repeat;
    }
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translateY(-50%);
      width: 14px;
      height: 20px;
      background: url("../../../assets/select-options.svg") center/cover no-repeat;
    }
    &.show {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      box-shadow: none;
    }
  }
  &Menu {
    width: 100%;
    position: absolute;
    top: 33px;
    left: 0;
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    border-bottom-left-radius: 20.5px;
    border-bottom-right-radius: 20.5px;
    padding-top: 5px;
    padding-left: 10px;
    overflow-y: scroll;
    &.show {
      visibility: visible;
      opacity: 1;
    }
    &_item {
      width: 100%;
      font-size: 13.2px;
      font-family: "Montserrat", sans-serif;
      color: #707070;
      text-align: center;
      cursor: pointer;
      border-bottom: 1px solid rgba(0, 0, 0, 0.514);
      padding: 5px 0;
      opacity: 0.6;
      &:hover {
        opacity: 1;
      }
    }
  }
}
