.code {
  &__recover {
    cursor: pointer;
    span {
      font-weight: bold;
      color: #ffffff;
      font-size: 10px;
      text-align: center;
      text-decoration: underline;
    }
  }
  &Container {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    padding-top: 15px;
    padding-bottom: 0.4rem;

    h5 {
      font-family: "Montserrat", sans-serif;
      font-size: 1.2rem;
      font-weight: 300;
      color: #ffffff;
      margin-right: 12px;
    }

    h3 {
      margin-top: 0 !important;
      font-size: 1rem;
    }
  }
}
