@import "../../../utils/media.scss";

@keyframes playAnimation {
  from {
    transform: translate(-50%, -50%) scale(1.05);
  }

  to {
    transform: translate(-50%, -50%) scale(0.95);
  }
}

.sliderWrapper {
  width: 100%;
}

.button {
  background: none;
  outline: none;
  border: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  &.next {
    right: 10px;
  }

  &.back {
    left: 10px;
  }
}

.slider {
  position: relative;
  overflow: hidden;
  overflow-x: hidden;
  overflow-y: visible;
  // padding: 4rem 0;
  padding: 0;
  margin: 4rem 0;
  padding-left: 58px;
  transition: all 300ms ease-in-out;

  @include respond(phone) {
    padding-left: 0;
  }

  &:hover {
    // transform: translate3d(-2.75vw, 0, 0);
    transition: all 300ms ease-in-out;
    padding: 4rem 0;
    padding-left: 58px;
    margin: 0;
    @include respond(phone) {
      padding-left: 0;
    }
  }

  li {
    transition: all 300ms ease-in-out;
  }

  &:hover li {
    // opacity: 0.3;

    &:hover {
      opacity: 1;
      transform: scale(1.3) translate3d(0.2vw, 0, 0);
      z-index: 1000;
      @include respond(phone) {
        transform: none;
      }
      ~ li {
        transform: translate3d(3.5vw, 0px, 0px) !important;
        @include respond(phone) {
          transform: none !important;
        }
      }
    }
  }

  &:hover li:not(:hover) {
    transform: translate3d(-3vw, 0px, 0px);
    @include respond(phone) {
      transform: none;
    }
  }
}
