@import "../../utils/media.scss";

.slider {
  &Container {
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    @include respond(phone) {
      padding-left: 2rem;
    }

    // padding-top: 1rem;
    // // padding-left: 4rem;
    // padding-bottom: 2rem;
    h2 {
      color: #f5f5f5;
      font-family: "Montserrat", sans-serif;
      font-size: 1.8rem;
      font-weight: 600;
      letter-spacing: 0.02px;
      line-height: 22px;
      text-align: left;
      margin-left: 58px;
      margin-bottom: -3rem;

      @include respond(phone) {
        margin-left: .5rem;
      }
    }
  }

  &List {
    width: 100%;
    padding: 1rem 0;
    position: relative;
  }
}