@import "../../../utils/media.scss";
@import "../../../utils/animations.scss";

.buttonForm {
  position: relative;
  display: block;
  width: 100%;
  height: 40px;
  background-color: #ffc404;
  border: none;
  outline: none;
  color: #000;
  font-family: "Montserrat", sans-serif;
  font-size: 1.4rem;
  font-weight: bold;
  letter-spacing: -0.19px;
  border-radius: 56px;
  text-transform: uppercase;
  cursor: pointer;
  position: relative;
  z-index: 100;
  transition: all 200ms ease-in-out;
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    width: 15.14px;
    height: 15.98px;
    background: url("../../../assets/play-button.svg") center / cover no-repeat;
  }

  &:hover {
    // border-color: #fff !important;
    background-color: #11111e;
    border: 1px solid #ffc404;
    color: #fff;
  }
}
