.error {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  padding-left: 4rem;
  color: #fff;
  text-align: left;
  h1 {
    font-size: 40px;
    font-weight: 400;
    margin-bottom: 20px;
  }
  h2 {
    font-size: 32px;
    margin-bottom: 20px;
  }
  p {
    font-size: 14px;
    margin-bottom: 20px;
  }
  &_refresh {
    font-weight: bold;
    cursor: pointer;
    text-decoration: underline;
  }
}
