@import "../../../utils/media.scss";
@import "../../../utils/animations.scss";

.contentInfo {
  &Container {
    width: 100%;
    max-width: 540px;
    // max-width: 50%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    // animation: translateFromBottomWithOpacity 500ms ease-in-out;
    // animation-delay: 300ms;
    // animation-fill-mode: backwards;
    z-index: 100;

    &.animation {
      animation: translateFromBottomWithOpacity 500ms ease-in-out;
      animation-delay: 300ms;
      animation-fill-mode: backwards;
    }

    &_info {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      z-index: 100;

      @include respond(phone) {
        // display: none;
      }

      &.fromLanding {
        // padding-left: 58px;
        @include respond(phone) {
          // margin-top: 100px;
          // display: none;
        }
      }
    }

    h2 {
      color: #ffffff;
      font-family: "Montserrat", sans-serif;
      font-size: 4.5rem;
      font-weight: 600;
      text-align: left;
      margin-bottom: 10px;
      &.minTitle {
        font-size: 3rem;
      }

      @include respond(phone) {
        margin-bottom: 0;
        font-size: 30px;
        margin: 24px 0 10px 0;
      }
    }

    h3 {
      color: #f5f5f5;
      font-family: "Montserrat", sans-serif;
      font-size: 1.4rem;
      text-align: left;
      letter-spacing: -0.04px;
      line-height: 17px;
      margin-bottom: 10px;
      font-weight: 600;
      @include respond(phone) {
        font-size: 14px;
      }
    }

    p {
      color: #a0a1a1;
      font-family: "Montserrat";
      font-size: 1.5rem;
      font-weight: 500;
      text-align: left;
      max-width: 500px;
      letter-spacing: -0.12px;
      line-height: 18px;
      max-height: 76px;
      overflow: hidden;
    }

    .description {
      // animation: translateFromBottomWithOpacity 500ms ease-in-out;
      animation-delay: 1100ms;
      animation-fill-mode: backwards;
    }

    .creators {
      color: gray;
    }
  }

  &_videoStatus {
    width: 100%;
    z-index: 100;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin: 10px 0;
    // animation: translateFromBottomWithOpacity 500ms ease-in-out;
    animation-delay: 1300ms;
    animation-fill-mode: backwards;

    @include respond(phone) {
      margin: 0;
    }

    &.cantPurchase {
      margin-bottom: 0;
    }

    &.fromLanding {
      // padding-left: 58px;
    }

    &_coin {
      width: 23px;
      height: 23px;
    }

    span {
      z-index: 100;
      color: #ffffff;
      font-family: "Montserrat", sans-serif;
      font-size: 2.1rem;
      font-weight: 600;
      line-height: 36px;
      text-align: left;
      &.cantPurchase {
        font-size: 16px;
        color: #ff0a0a;
      }

      @include respond(phone) {
        font-size: 1.4rem;
        line-height: 20px;

        // &:last-of-type {
        //   margin-left: 1rem;
        // }
      }
    }

    svg {
      z-index: 100;
      margin: 0 1rem;

      @include respond(phone) {
        display: none;
      }
    }

    &.fromNextEpisode {
      margin: 8px 0 15px 0;

      span {
        font-size: 20px;
      }

      svg {
        width: 23px;
      }
    }
  }
}

