.buttonAnimation {
  // animation: translateFromBottomWithOpacity 500ms ease-in-out;
  // animation-delay: 1500ms;
  // animation-fill-mode: backwards;
}

.fromNextEpisode {
  h2 {
    display: none;
  }

  h3 {
    font-size: 25px;
  }

  .titleMask {
    animation: none;
    overflow: visible;
    margin-bottom: 5px;
  }

  p {
    max-height: 40px;
  }
}

.buttonContainer {
  width: 100%;

  @include respond(phone) {
    display: flex;
    align-items: center;
    justify-content: center;
    button {
      // margin: 10px 0 50px !important;
      margin-top: 80px;
      max-width: 275px;
    }
  }

  &.fromNextEpisode {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
