@import "../../utils/media.scss";

.notfound {
  width: 100%;
  min-height: 80vh;
  padding-top: 100px;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  text-align: center;
  background-image: linear-gradient(to bottom, #45188e, #211f35 100%);

  .oops {
    margin-bottom: 38px;
    @include respond(phone) {
      width: 345px;
      height: 65px;
      margin-bottom: 27px;
    }
  }
  .error {
    margin-bottom: 24px;
    width: 152px;
    height: 59px;
    @include respond(phone) {
      width: 107px;
      height: 41px;
      margin-bottom: 27px;
    }
  }
  h1 {
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 20px;
    @include respond(phone) {
      font-size: 21px;
    }
  }

  &_button {
    width: 199px;
    height: 40px;

    @include respond(phone) {
      width: 223px;
      height: 45px;
      font-weight: bold;
    }

    &::after {
      content: none;
    }
    &::before {
      content: none;
    }
  }
}
