@import "../../utils/media.scss";

.nav {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 6000;
  transition: all ease-in-out 200ms;

  &.scrolled {
    background: rgba(#11111e, 1);
    transition: all ease-in-out 200ms;
  }

  @include respond(phone) {
    background: #11111e !important;
  }

  &Container {
    max-width: 100%;
    padding: 2rem 58px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;

    @include respond(tab-land) {
      padding: 2rem 38px;
    }

    @include respond(tab-port) {
      padding: 2rem 28px;
    }
    @include respond(phone) {
      padding: 10px 20px;
    }
  }

  &__actions {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    min-height: 37px;
    max-height: 37px;
    &.isMobile {
      justify-content: space-between;
    }

    &_logo {
      position: relative;
      margin-left: 6rem;
      margin-right: 2rem;

      @include respond(phone) {
        margin: 0;
      }

      // width: 81.48px;
      // height: 25.3px;
      &_lottie {
        // position: absolute;
        // top: 0;
        // left: 0;
        width: 114px;
        margin-bottom: 10px;
        // height: 28px;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50%, -50%);

        [stroke="rgb(63,59,105)"],
        [fill="rgb(63,59,105)"] {
          stroke: #fff !important;
          fill: #fff !important;
        }
      }
    }

    &_burger {
      margin-right: 4rem;
      align-self: center;
      cursor: pointer;
      // width: 20px;
      // height: 13px;
    }
  }

  &__winFlixx {
    width: fit-content;
    display: flex;
    justify-content: center;

    @include respond(phone) {
      width: 100%;
    }
  }

  &__user {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include respond(desktop) {
      width: 80%;
    }

    @include respond(tab-land) {
      width: 50%;
    }

    @include respond(tab-port) {
      width: fit-content;
    }
    @include respond(phone) {
      display: none;
    }

    &__svg {
      width: 25px;
      height: 25px;
    }
    &__coins {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 1.5rem;

      @include respond(phone) {
        margin-right: 0;
      }

      svg {
        width: 24.66px;
        margin-right: 1rem;
      }

      span {
        color: #fefefe;
        font-family: "Montserrat", sans-serif;
        font-size: 1.6rem;
        font-weight: 600;
        line-height: 19px;
        text-align: left;
      }
    }

    &__image {
      border: 1px solid #ffffff;
      border-radius: 100%;
      width: 26.91px;
      height: 26.91px;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;

      @include respond(tab-port) {
        display: none;
      }

      img {
        width: 95%;
        border-radius: 100%;
      }
    }
  }

  &Button {
    margin: 0 auto;
    background-color: #6432d8;
    border-radius: 12.5px;
    width: 100%;
    color: #fefefe;
    font-family: "Montserrat", sans-serif;
    font-size: 1rem;
    font-weight: 400;
    text-align: center;
    border: none;
    cursor: pointer;
    outline: none;
    font-weight: 700;
    width: 190px;
    height: 25px;

    @include respond(phone) {
      max-width: 120px;
      padding: 1rem 1rem;
      margin: 0 0 0 auto;
    }

    &__signIn {
      color: #11111e;
      background-color: #ffc404;
      margin: 0;
    }
  }
}

.burger {
  position: fixed;
  top: 25px;
  left: 58px;
  z-index: 10000;
  width: 25px;
  cursor: pointer;
}

.isSearching {
  visibility: hidden !important;
  opacity: 0 !important;
}
