@import "../../utils/media.scss";

.modalAlert {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #504696;
  width: 100%;
  max-width: 980px;
  height: 645px;
  z-index: 15001;
  background-position: center;
  background-size: cover;
  padding: 58px 0;
  display: flex;
  align-items: center;
  border-radius: 10px;

  @include respond(phone) {
    max-width: 90%;
    max-height: 516px;
    padding: 40px 0;
  }

  &.playAsAnon,
  &.register,
  &.validate,
  &.mobile {
    background-image: url("../../assets/playasanon.png");
  }

  &.recover {
    background-image: url("../../assets/recovermask.png");
  }

  &.signIn {
    background-image: url("../../assets/loginmask.png");
  }

  &Left {
    min-width: 432px;
    max-width: 432px;
    border-right: 1px solid #70707050;
    height: 100%;

    @include respond(phone) {
      display: none;
    }
  }

  &Right {
    width: 100%;
    max-width: 547px;
    height: 100%;
    position: relative;

    &Cont {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: 0;

      @include respond(phone) {
        width: 100%;
      }
    }
  }

  &-container {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
  }

  &Overlay {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba($color: #000000, $alpha: 0.85);
    z-index: 15000;
  }

  &__header {
    width: 100%;

    // padding: 0 0 67px 0;
    img {
      display: block;
      margin: 0 auto;
      width: 100%;
      max-width: 255px;
    }
  }

  &__content {
    width: 100%;
    padding: 0 60px;

    &__title {
      color: #ffffff;
      font-size: 18px;
      font-weight: bold;
      text-align: center;
      margin-bottom: 10px;
      margin-top: 67px;

      &.recover {
        margin-top: 10px;
      }
      &.register {
        margin-top: 24px;
      }

      span {
        color: #ffc900;
      }
    }

    &__subTitle {
      color: #ffffff;
      font-size: 14px;
      font-weight: bold;
      line-height: normal;
      text-align: center;
      margin-bottom: 0;
    }

    &Section {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding-top: 30px;
      // &.playAsAnon,
      // &.validate,
      // &.mobile {
      //   padding-top: 30px;
      // }

      // &.recover {
      //   padding-top: 30px;
      // }

      // &.register {
      //   padding-top: 10px;
      // }

      &__title {
        font-family: "Montserrat", sans-serif;
        color: #ffffff;
        font-size: 14px;
        font-weight: lighter;
        text-align: left;
        margin-bottom: 0;
      }
    }
  }

  &__input {
    background-color: #ffffff;
    border-radius: 20.5px;
    box-shadow: 0 2px 18px 0 rgba(0, 0, 0, 0.2);
    border: none;
    color: #707070;
    font-size: 13.2px;
    font-weight: 300;
    text-align: left;
    padding: 10px 0 10px 70px;
    width: 100%;
    outline: none;
    font-weight: 500;

    &CountryCode {
      padding: 10px;
      width: 60px;
      text-align: center;
      pointer-events: none;
      margin-right: 10px;
    }
    &Recover {
      padding-left: 10px;
    }

    &::placeholder {
      color: #707070cb;
    }

    &Eye {
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translateY(-50%);
      width: 18px;
      height: 12px;
      cursor: pointer;
      background: url("../../assets/eye.svg") center / cover no-repeat;
      opacity: 0.3;

      &.hide {
        background: url("../../assets/hide.svg") center / cover no-repeat;
      }
    }

    &Container {
      width: 100%;
      position: relative;

      &:first-of-type {
        margin-bottom: 22px;
      }

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translateY(-50%);
        width: 32px;
        height: 32px;
        background: url("../../assets/username-input.svg") center / cover no-repeat;
      }

      &__code::before {
        content: none;
      }

      &__password::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translateY(-50%);
        width: 32px;
        height: 32px;
        background: url("../../assets/password-input.svg") center / cover no-repeat;
      }

      &__mobile::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translateY(-50%);
        width: 32px;
        height: 32px;
        background-color: red;
        background: url("../../assets/mobile.svg") center / cover no-repeat;
      }
    }
  }

  &__subText {
    color: #ffffff;
    font-size: 14px;
    font-weight: 300;
    line-height: 17px;
    text-align: center;
    @include respond(phone) {
      font-size: 11px;
    }

    &.recover {
      text-decoration: underline;
      cursor: pointer;
      margin-top: 28px;
      margin-bottom: 12px;
    }

    span {
      font-weight: bold;
    }
  }

  &__register {
    &Container {
      margin-top: 20px;
      display: flex;
      width: 100%;
      justify-content: center;
      // padding-top: 10px;
    }
    button {
      background: transparent;
      border: none;
      width: fit-content;
      padding: 0;
    }

    cursor: pointer;
    // background-color: #a0a1a1;
    border-radius: 100%;
    width: 51.65px;
    height: 51.65px;
    display: flex;
    align-items: center;
    justify-content: center;

    &:first-of-type {
      // margin-right: 50px;
    }
  }

  &__recoverInputContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__stores {
    width: 175px;
    height: 52px;
    width: 140px;
    height: 42px;
    @include respond(phone) {
      width: 140px;
      height: 42px;
      &:first-of-type {
        margin-right: 14px;
      }
    }
    &:first-of-type {
      margin-right: 17px;
    }
    img {
      width: 100%;
    }

    &Container {
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: center;
      margin-top: 29px;
    }
  }
}

.buttonModalAlert {
  &:first-of-type {
    margin-bottom: 30px;
  }
}

.sign {
  margin: 16px 0;
  width: 100%;
  min-width: 100%;
}

.playAsAnonMod {
  margin: 16px 0 30px 0;
  width: 100%;
  min-width: 100%;
  &:last-of-type {
    margin: 0 0 28px 0;
  }
}

.m20 {
  margin-bottom: 22px;
}

.error {
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #ff0a0a;
}

.errorBorder {
  border: 2px solid #ff0a0a;
}

.closeModalSvg {
  position: absolute;
  top: 31px;
  right: 37px;
  cursor: pointer;

  @include respond(phone) {
    top: 20px;
    right: 20px;
  }
}

.form {
  width: 100%;
}
