.backdrop {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  background: rgba(black, 0.9);
}

.modalAd {
  // width: 100%;
  // z-index: 1010;
  // position: absolute;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);
  // display: flex;
  // align-items: center;
  // justify-content: center;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #504696;
  width: 100%;
  max-width: 550px;
  height: 645px;
  z-index: 15001;
  background-position: center;
  background-size: cover;
  padding: 0 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  background-image: url("../../../assets/modal-ad-mask.png"), linear-gradient(#45188e, #211f35);
  font-family: "Montserrat", sans-serif;
  span {
    color: #ffc900;
  }
  .play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    transition: all 150ms ease-in-out;
    z-index: 10;
    &:hover {
      transform: translate(-50%, -50%) scale(1.1);
    }
  }
  &Footer {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
  }

  &_title {
    color: #ffffff;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 12px;
  }
  &_subTitle {
    color: #ffffff;
    font-size: 14px;
    font-weight: bold;
    line-height: normal;
    text-align: center;
  }
  &_subText {
    color: #ffffff;
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
    text-align: center;
  }

  &_stores {
    width: 175px;
    height: 52px;
    // @include respond(phone) {
    //   width: 140px;
    //   height: 42px;
    //   &:first-of-type {
    //     margin-right: 14px;
    //   }
    // }
    &:first-of-type {
      margin-right: 17px;
    }
    img {
      width: 100%;
    }

    &Container {
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: center;
      margin-top: 29px;
    }
  }
  &_player {
    margin-top: 60px;
    margin-bottom: 50px;
    width: 370px;
    position: relative;
    // height: 205px;
  }

  &_close {
    position: absolute;
    top: 31px;
    right: 37px;
    cursor: pointer;
  }
}
