@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@import "../../../utils/animations.scss";
@import "../../../utils/media.scss";

.switchSeason {
  width: 149px;
  height: 25px;
  border-radius: 12.5px;
  border: solid 1px #ffffff;
  position: relative;
  z-index: 5000;
  padding-left: 19px;
  // margin: 3.5rem 0;
  // margin-left: 58px;
  margin-top: 20px;
  animation: translateFromBottomWithOpacity 500ms ease-in-out;
  animation-fill-mode: backwards;
  animation-delay: 600ms;

  &.mobile {
    @media only screen and (min-width: 601px) {
      display: none;
    }
  }

  &.desktop {
    @include respond(phone) {
      display: none;
    }
  }

  &.isShowing {
    border-radius: 0px;
    border-bottom: none;
    border-top-left-radius: 12.5px;
    border-top-right-radius: 12.5px;

    @include respond(phone) {
      background-color: #11111efb;
    }
  }

  &Current {
    font-family: "Montserrat", sans-serif;
    font-size: 13px;
    font-weight: 500;
    color: #ffffff;
    line-height: 22.5px;
    text-align: left;
    cursor: pointer;
  }

  &Arrow {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    height: 9px;
    width: 9px;
  }

  &Options {
    width: 149px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 10px;
    background-color: transparent;
    position: absolute;
    border: solid 1px #ffffff;
    border-top: none;
    border-bottom-left-radius: 12.5px;
    border-bottom-right-radius: 12.5px;
    top: 20px;
    overflow: hidden;
    left: -1px;

    @include respond(phone) {
      background-color: #11111efb;
    }

    &Item {
      width: 100%;
      text-align: center;
      font-family: "Montserrat", sans-serif;
      font-size: 1.4rem;
      font-weight: 500;
      color: #ffffff;
      text-align: center;
      padding: 10px 0;
      cursor: pointer;
      transition: all 200ms ease-in-out;

      &:hover {
        background-color: #6432d8;
      }
    }
  }
}
