@keyframes loadingStatus {
  from {
    background-color: #11111e;
  }

  to {
    background-color: #211f35;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@import "../../utils/media.scss";

.hero {
  width: 100%;
  position: relative;
  background: #11111e;

  @include respond(phone) {
    padding-top: 90px;
  }

  &.isLoading {
    height: 80vh;
    @include respond(phone) {
      height: 50vh;
    }
  }

  &.opacityAnimation {
    animation: fadeIn 1s ease-in-out;
  }
}

.background {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  height: 100%;
  max-height: 80vh;
  animation: fadeIn 1s ease-in-out;

  &LeftGradient {
    width: 70%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
    background: linear-gradient(to right, rgba(17, 17, 30, 1) 0%, rgba(17, 17, 30, 0.8) 50%, rgba(17, 17, 30, 0.6951155462184874) 70%, rgba(17, 17, 30, 0) 100%);
    z-index: 0;
  }

  &BottomGradient {
    width: 100%;
    height: 82px;
    position: absolute;
    left: 0;
    bottom: 0;
    // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
    background: linear-gradient(to top, rgba(17, 17, 30, 0.99) 0%, rgba(0, 0, 0, 0) 100%);
    z-index: 0;
  }
}
