@import "../../../utils/media.scss";
.sideNav {
  width: 33%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #11111e;
  z-index: 9000;
  // box-shadow: 17px 0px 45px 10px rgba(#11111e, 0.75);
  opacity: 0;
  visibility: hidden;
  transform: translateX(-1000px);
  transition: all 300ms ease-in-out;
  padding-left: 58px;
  padding-right: 58px;
  padding-top: 10rem;
  @include respond(phone) {
    width: 100%;
  }
  &.show {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
  &Options {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    border-bottom: solid 2px #707070;
    padding-bottom: 30px;
  }
  &Option {
    font-family: "Montserrat", sans-serif;
    font-size: 1.2rem;
    font-weight: 500;
    color: #707070;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 15px;
    cursor: pointer;
    span {
      width: 8px;
      height: 1px;
      display: block;
      background-color: #707070;
      margin-right: 20px;
    }
  }
}
.close {
  position: absolute;
  top: 26px;
  left: 46px;
  width: 14px;
  height: 15px;
  cursor: pointer;
}
