@import "../../../utils/animations.scss";
@import "../../../utils/media.scss";

.goBack {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 5000;
  cursor: pointer;
  width: 100%;
  padding-left: 58px;
  margin-bottom: 50px;
  animation: fadeIn 500ms ease-in-out;
  animation-delay: 900ms;
  animation-fill-mode: backwards;

  &.fromModal {
    position: absolute;
    top: 31px;
    left: 37px;
    width: fit-content;
    padding: 0;
    animation: none;
    span {
      display: none;
    }
  }

  @include respond(phone) {
    display: none;
  }

  &ArrowContainer {
    width: 25px;
    height: 25px;
    background-color: #212139;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  svg {
    width: 8px;
    height: 14px;
  }

  span {
    font-family: "Montserrat", sans-serif;
    font-size: 1rem;
    font-weight: 600;
    color: #fefefe;
    margin-left: 1rem;
  }
}
