@import "../../utils/animations.scss";
@import "../../utils/media.scss";

.headerLanding {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 58px;
  margin-bottom: 54px;
  min-height: 335px;

  @include respond(phone) {
    padding: 0 20px;
    flex-direction: column;
  }

  &_caratula {
    width: 100%;
    height: 229px;
    border-radius: 10px;
    background-color: #d8d8d8;
    z-index: 100;
    // animation: translateFromRightWithOpacity 500ms ease-in-out;
    // // animation-delay: 1.25s;
    // animation-fill-mode: forwards;

    &.animation {
      animation: translateFromRightWithOpacity 500ms ease-in-out;
      // animation-delay: 1.25s;
      animation-fill-mode: forwards;
    }

    @include respond(phone) {
      height: 138px;
    }

    &container {
      width: 412px;
      display: flex;
      margin-right: 36px;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;

      @include respond(phone) {
        width: 100%;
        margin-right: 0;
        max-width: 246px;
      }
    }

    img {
      border-radius: 10px;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}
