@import "../../../utils/media.scss";
@import "../../../utils/animations.scss";

.carousel {
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 659px;
  position: relative;

  .contentInfo {
    @include respond(phone) {
      padding: 0 20px;
      margin-top: 100px;
      width: 100%;
      z-index: 100;
    }
  }

  @include respond(phone) {
    // height: auto;
    // padding-top: 200px;
    height: 340px;
  }

  &Background {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    height: 100%;
    max-height: 80vh;
    animation: fadeIn 1s ease-in-out;

    @include respond(phone) {
      height: 220px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      max-width: 100%;
      position: absolute;
      top: 0;
      right: 0;
      z-index: -1;
      // @include respond(phone) {
      //   object-fit: contain;
      // }
    }
  }

  &LeftGradient {
    width: 50%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
    // background: linear-gradient(to right, rgba(17, 17, 30, 1) 0%, rgba(17, 17, 30, 0.8) 50%, rgba(17, 17, 30, 0.6951155462184874) 70%, rgba(17, 17, 30, 0) 100%);
    background-image: linear-gradient(to right, rgba(17, 17, 30, 1) 0%, rgba(17, 17, 30, 1) 40%, rgba(0, 0, 0, 0) 100%);
    z-index: 10;

    @include respond(phone) {
      display: none;
    }
  }

  &BottomGradient {
    width: 100%;
    height: 82px;
    height: 219px;
    position: absolute;
    left: 0;
    bottom: -136px;
    // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
    // background: linear-gradient(to top, rgba(17, 17, 30, 1) 0%, rgba(17, 17, 30, 0.3) 35%, rgba(0, 0, 0, 0.3) 80%, rgba(0, 0, 0, 0.3) 100%);
    background-image: linear-gradient(to top, rgba(17, 17, 30, 0.99) 61%, rgba(0, 0, 0, 0));
    z-index: 10;

    @include respond(phone) {
      height: 240px;
      background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(17, 17, 31, 0.1) 20%, rgba(17, 17, 31, 1) 40%);
    }
  }
}

.hidden {
  transition: opacity 500ms ease-in-out;
  opacity: 0;
}

.visible {
  transition: opacity 500ms ease-in-out;
  opacity: 1;
}

