@keyframes loadingStatus {
  from {
    background-color: #11111e;
  }
  to {
    background-color: #211f35;
  }
}

@keyframes loadingStatus2 {
  from {
    background-color: #211f35;
  }
  to {
    background-color: #2e2b4b;
  }
}

@import "../../../utils/media.scss";

.sliderSkeleton {
  width: 100%;
  padding: 0;
  padding-left: 58px;
  display: flex;
  justify-content: space-between;
  @include respond(phone) {
    padding-left: 1rem;
  }
  &Item {
    width: 100%;
    height: 137px;
    animation: loadingStatus2 1.5s ease-in-out infinite;
    margin-right: 10px;
    border-radius: 10px;
    @include respond(phone) {
      height: 94px;
    }
  }
}
