.subtitle {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #ffffff;
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
}

.form {
  width: 100%;
  max-width: 310px;
  margin: 0 auto;
  .inputContainer {
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 18px;
    }
    margin-top: 19px;
    width: 100%;
    position: relative;

    &.nickname {
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translateY(-50%);
        width: 33px;
        height: 32px;
        background: url("../../../../assets/username-input.svg");
      }
    }

    &.email {
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translateY(-50%);
        width: 33px;
        height: 33px;
        background: url("../../../../assets/mail-input.svg");
        background-size: cover;
      }
    }

    &.password {
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 5px;
        transform: translateY(-50%);
        width: 33px;
        height: 32px;
        background: url("../../../../assets/password-input.svg");
      }
    }

    input {
      background-color: #ffffff;
      border-radius: 20.5px;
      box-shadow: 0 2px 18px 0 rgba(0, 0, 0, 0.2);
      border: none;
      color: #707070;
      font-size: 13.2px;
      font-weight: 300;
      text-align: left;
      padding: 13px 0 10px 70px;
      width: 100%;
      outline: none;
      font-weight: 500;
    }
  }

  .select {
    margin-top: 40px;
    width: 100%;
    max-width: 310px;
    height: 41px;
    background-color: white;
    border-radius: 100px;
    overflow: hidden;
    padding: 0 20px 0 45px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 5px;
      transform: translateY(-50%);
      width: 33px;
      height: 33px;
      background: url("../../../../assets/flag-input.svg");
    }

    select {
      width: 100%;
      height: 100%;
      border-radius: 100px;
      border: none;
      font-family: "Montserrat", sans-serif;
      color: #707070;
      option {
        font-family: "Montserrat", sans-serif;
        color: #707070;
      }
    }
  }

  .phoneInput {
    margin-top: 26px;
    width: 100%;
    max-width: 310px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    input {
      height: 41px;
      border-radius: 100px;
      background-color: white;
      border: none;
      width: 216px;
      font-size: 13.3px;
      font-family: "Montserrat", sans-serif;
      padding: 0 46px 0 21px;
      &:first-child {
        width: 68px;
        padding: 0 17px;
      }
    }
  }
}

.btn {
  width: 100%;
  margin-top: 18px;
  margin-bottom: 20px;
  max-width: 100%;
}

.registerForm {
  &__recoverInputContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
  &__input {
    background-color: #ffffff;
    border-radius: 20.5px;
    box-shadow: 0 2px 18px 0 rgba(0, 0, 0, 0.2);
    border: none;
    color: #707070;
    font-size: 13.2px;
    font-weight: 300;
    text-align: left;
    padding: 10px 0 10px 70px;
    width: 100%;
    outline: none;
    font-weight: 500;

    &CountryCode {
      padding: 10px;
      width: 60px;
      text-align: center;
      pointer-events: none;
      margin-right: 10px;
    }
    &Recover {
      padding-left: 10px;
    }

    &::placeholder {
      color: #707070cb;
    }
  }
}

.error {
  color: white;
  font-size: 12px;
  margin-top: 5px;
  display: block;
  text-align: center;
  font-weight: 600;
  color: #ff0a0a;
}
