@import "../../../utils/animations.scss";

.maker {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 100;
  margin-top: 15px;
  margin-bottom: 5px;
  // animation: translateFromBottomWithOpacity 500ms ease-in-out;
  // animation-delay: 900ms;
  // animation-fill-mode: backwards;
  &Type {
    color: #ffc900;
    font-family: "Montserrat", sans-serif;
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: 0.01px;

    text-align: left;
    margin: 0 1rem;
  }
  &Image {
    border: 1px solid #ffffff;
    border-radius: 100%;
    width: 26.91px;
    height: 26.91px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    img {
      width: 95%;
      border-radius: 100%;
    }
  }
  &Duration {
    display: flex;
    align-items: center;
    margin-left: 30px;
    &Val {
      font-family: "Montserrat", sans-serif;
      font-size: 1.4rem;
      font-weight: 600;
      letter-spacing: 0.01px;
      text-align: left;
      color: #fff;
      text-decoration: underline;
      opacity: 1;
      margin-right: 1rem;
    }
  }
  &User {
    color: #f9f9f9;
    font-family: "Montserrat", sans-serif;
    font-size: 1.4rem;
    font-weight: 600;
    text-align: left;
    text-decoration: underline;
  }
}
