@import "../../../utils/media.scss";

.modalSuccess {
  position: fixed;
  top: 50%;
  left: 50%;
  // transform: translate(-50%, -50%);
  background: linear-gradient(0deg, rgba(33, 31, 53, 1) 0%, rgba(69, 24, 142, 1) 100%);
  width: 100%;
  max-width: 550px;
  height: 645px;
  z-index: 15001;
  background-position: center;
  background-size: cover;
  padding: 58px 0;
  display: flex;
  align-items: center;
  border-radius: 10px;
  animation: openModal 500ms;
  animation-fill-mode: forwards;
  @include respond(phone) {
    max-width: 378px;
    height: 516px;
  }
  &_lottie {
    margin-bottom: 86px;
    [stroke="rgb(63,59,105)"],
    [fill="rgb(63,59,105)"] {
      stroke: #fff !important;
      fill: #fff !important;
    }
  }
  &Overlay {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba($color: #000000, $alpha: 0.85);
    z-index: 15000;
  }
  .closeModalSvg {
    position: absolute;
    top: 31px;
    right: 37px;
    cursor: pointer;

    @include respond(phone) {
      top: 20px;
      right: 20px;
    }
  }
  &_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  &_button {
    max-width: 226px;
    font-size: 13px;
    &::after,
    &::before {
      content: none;
    }
  }
  h2,
  h3 {
    font-family: "Montserrat", sans-serif;
    color: #ffffff;
    text-align: center;
    font-weight: bold;
  }
  h2 {
    font-size: 24px;
    margin-bottom: 25px;
    @include respond(phone) {
      font-size: 18px;
      margin-bottom: 18px;
    }
  }
  h3 {
    font-size: 14px;
    margin-bottom: 90px;
    @include respond(phone) {
      font-size: 12px;
      margin-bottom: 90px;
    }
    span {
      color: #ffc900;
    }
  }
}

@keyframes openModal {
  0% {
    transform: translate(-50%, -50%) scale(1.1);
  }
  50% {
    transform: translate(-50%, -50%) scale(0.9);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}
