@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@import "../../../utils/media.scss";

.slide {
  animation: fadeIn 1s ease-in-out;
  animation-fill-mode: backwards;

  @include respond(phone) {
    padding-bottom: 100% !important;
  }
}

.serieSlider {
  width: 100%;
  max-width: 245px;
  cursor: pointer;

  &Episode {
    position: absolute;
    bottom: 10px;
    right: 17px;
    color: #fcfcfb;
    font-family: "Montserrat", sans-serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 36px;
    z-index: 100000;
  }

  &Thumbnail {
    width: 100%;
    height: 138px;
    transition: all 200ms ease-in-out;
    position: relative;
    cursor: pointer;

    // &::after {
    //   content: "";
    //   opacity: 0;
    //   visibility: 0;
    //   transition: all 300ms ease-in-out;
    //   position: absolute;
    //   width: 50px;
    //   height: 50px;
    //   top: 50%;
    //   left: 50%;
    //   transform: translate(-50%, -50%);
    //   background: url("../../../assets/play.svg") no-repeat;
    //   background-size: cover;
    //   background-position: center;
    //   z-index: 1001;
    //   pointer-events: none;
    // }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 10px;
      border: 2px solid transparent;

      &:hover {
        // border: 2px solid #ffc404;
      }
    }

    &:hover {
      // border: 2px solid #ffc404;
      &::after {
        opacity: 1;
        visibility: 1;
      }
    }
  }

  &Info {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 17px;

    h3,
    h4,
    p {
      text-align: left;
      color: #ffffff;
      font-family: "Montserrat", sans-serif;
      font-size: 1.2rem;
      letter-spacing: normal;
    }

    h4,
    p {
      color: #fcfcfb;
    }

    h3 {
      font-weight: 500;
      // margin: 14px 0 12px 0;
    }

    h4 {
      font-weight: 600;
      line-height: 14px;
      margin-bottom: 8px;
    }

    p {
      font-weight: 300;
      line-height: 14px;
      height: 75px;
      max-height: 75px;
      overflow: hidden;
    }
  }

  &Button {
    background-color: #6432d8;
    border: 1.38px solid transparent;
    border-radius: 100px;
    color: #fefefe;
    font-family: "Montserrat", sans-serif;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    font-weight: 500;
    cursor: pointer;
    width: 100px;
    height: 25px;

    &.toPurchase {
      background-color: #ffc404;
      border: 1.38px solid #ffc404;
      color: #11111e;
      font-weight: bold;
    }
  }
}

