.gradient {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 100%);
  border-radius: 10px;

  &:hover {
    border: 1.38px solid #ffc404;
  }

  &.isSelected {
    border: 1.38px solid #ffc404;
  }
}
