@import "../../../utils/media.scss";

.switch {
  &Container {
    display: flex;
    width: fit-content;
    justify-content: center;
    align-items: center;
    margin-right: 3rem;

    @include respond(phone) {
      margin: 0;
      width: 100%;
      padding: 1rem 0;
    }
  }

  &Item {
    width: fit-content;
    padding: 0.5rem 1rem;
    cursor: pointer;
    transition: all 100ms ease-in-out;
    border-bottom: 2.5px solid transparent;

    &:first-of-type {
      margin-right: 14px;
    }

    &:last-of-type {
      margin-left: 14px;
    }

    @include respond(phone) {
      width: 100%;
    }

    h2 {
      color: #707070;
      font-family: "Montserrat", sans-serif;
      font-size: 1rem;
      font-weight: 600;
      text-align: left;
      line-height: 1px;

      @include respond(phone) {
        text-align: center;
        font-size: 1.6rem;
      }
    }

    &.active {
      // border-bottom: 2.5px solid #ffc404;
      position: relative;

      @include respond(phone) {
        &:first-of-type::after {
          border-radius: 0;
          border-top-right-radius: 200px;
          border-bottom-right-radius: 200px;
        }
      }

      &::after {
        position: absolute;
        content: "";
        height: 2.5px;
        width: 100%;
        bottom: -7px;
        left: 0;
        background-color: #ffc404;
        border-radius: 200px;
        @include respond(phone) {
          bottom: -15px;
        }

        @include respond(phone) {
          &:first-of-type::after {
            border-radius: 0;
            border-top-left-radius: 200px;
            border-bottom-left-radius: 200px;
          }
        }
      }

      h2 {
        color: #fefefe;
      }
    }
  }
}

.isSearching {
  visibility: hidden !important;
  opacity: 0 !important;
}
