.castButton {
  width: fit-content;
  max-width: 25px;
  max-height: 25px;
  background: transparent;
  outline: none;
  box-shadow: none;
  border: none;
  cursor: pointer;
  position: absolute;
  bottom: 45px;
  right: 220px;
  z-index: 100000;
  svg {
    width: 100%;
    height: 100%;
    fill: #ffff;
  }
}
