@import "../../utils/media.scss";

.search {
  width: 100%;
  min-height: 100vh;
  padding: 10rem;
  @include respond(phone) {
    padding: 80px 1rem 1rem 1rem;
    // display: flex;
    // align-items: center;
    // justify-content: center;
  }
  h1 {
    color: #ffff;
    margin-bottom: 3rem;
    span {
      color: #ffc404;
    }
  }
  &Container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    align-items: center;
  }
  .slide {
    width: 238px;
    height: 134px;
    img {
      width: 98%;
      object-fit: cover;
      border: 1px solid transparent;
      transition: border 100ms ease-in-out;
      &:hover {
        border-color: #ffc404;
      }
    }
  }
}

.searchInitial {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @include respond(phone) {
    align-items: center;
    justify-content: center;
    height: calc(100vh - 80px);
  }
  svg {
    opacity: 0.5;
    width: 100px;
    height: 100px;
  }
  h1,
  h2 {
    font-family: "Montserrat", sans-serif;
    font-size: 26px;
    font-weight: 500;
    color: #fefefe;
  }
  h1 {
    margin-bottom: 10px;
    margin-top: 20px;
  }
  h2 {
    font-size: 13px;
  }
}
