.video {
  &Container {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 12000;
    width: 100%;
    height: 100vh;
    background-color: black;
    &Close {
      position: absolute;
      top: 50px;
      right: 50px;
      z-index: 7000;
      cursor: pointer;
    }
    div {
      // width: 100% !important;
      // height: 100% !important;
    }
  }
  width: 100%;
}
