@import "../../../utils/media.scss";

.CarouselWrapper {
  width: 100%;

  &DotContainer {
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);

    @include respond(phone) {
      bottom: 30px;
    }
  }
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  outline: none;
  border: 0;
  background-color: #707070;
  // margin-right: 20px;

  &:not(:last-of-type) {
    margin-right: 20px;
  }
  @include respond(phone) {
    width: 9px;
    height: 9px;
    &:not(:last-of-type) {
      margin-right: 10px;
    }
  }
}

.button {
  background: none;
  outline: none;
  border: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  @include respond(phone) {
    display: none;
  }

  &.next {
    right: 10px;
  }

  &.back {
    left: 10px;
  }
}

.slider {
  width: 100%;
  height: 80vh;

  @include respond(phone) {
  }
}

.background {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  height: 100%;
  max-height: 80vh;
  animation: fadeIn 1s ease-in-out;

  &LeftGradient {
    width: 70%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
    background: linear-gradient(to right, rgba(17, 17, 30, 1) 0%, rgba(17, 17, 30, 0.8) 50%, rgba(17, 17, 30, 0.6951155462184874) 70%, rgba(17, 17, 30, 0) 100%);
    z-index: 0;
  }

  &BottomGradient {
    width: 100%;
    height: 82px;
    position: absolute;
    left: 0;
    bottom: 0;
    // background: linear-gradient(to right, black, rgba(0, 0, 0, 0));
    background: linear-gradient(to top, rgba(17, 17, 30, 0.99) 0%, rgba(0, 0, 0, 0) 100%);
    z-index: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.animation {
  transition-property: opacity;
  transition-duration: 500ms;
}

.hidden {
  opacity: 0;
}
