.init {
}

@keyframes animationtest {
  0% {
    transform: translateX(-325px) translateY(-45px);
  }
  100% {
    transform: translateX(0) translateY(-45px);
  }
}
.suc {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 1;
  visibility: visible;
  z-index: 10000;
  //   transition: transform 300ms ease-in-out;
  animation: animationtest 500ms ease-in-out;
  animation-fill-mode: backwards;
}

.svg {
  width: 247px;
  transition: all 300ms ease-in-out;
  cursor: pointer;
  z-index: 1000;
  &:hover {
    transform: scale(1.1);
  }
}
