@import "../../../../utils/media.scss";

.modalAlertCarousel {
  width: 100%;
  position: relative;

  &_dotContainer {
    // position: absolute;
    // bottom: 0;
    // left: 50%;
    // transform: translateX(-50%);
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
    margin-bottom: 25px;
    position: absolute;
    bottom: 50px;

    button.carousel__dot--selected {
      background-color: #ffc900 !important;
    }
    @include respond(phone) {
      margin-bottom: 20px;
    }
  }

  &_slider {
    width: 100%;
    height: 100%;
  }

  &_slide {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    height: 491px;
    @include respond(phone) {
      height: 393px;
    }

    &ImgContainer {
      //   width: 400px;
      //   height: 249px;
      //   position: relative;
      width: 100%;
      max-height: 491px;
      position: absolute;
      top: -25px;
      left: 0;

      @include respond(phone) {
        top: 0;
        left: 0;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: bottom;
      //   object-position: top;

      //   width: 506px;
      //   height: 491px;
      //   position: absolute;
      //   top: 0;
      //   left: 0;
    }

    h2 {
      max-width: 428px;
      font-family: "Montserrat", sans-serif;
      font-size: 1.8rem;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff;
      position: absolute;
      bottom: 100px;
      @include respond(phone) {
        font-size: 14px;
      }

      span {
        color: #ffc900;
      }
    }
  }
}

.dot {
  width: 8px;
  height: 8px;
  border-radius: 100%;
  outline: none;
  border: 0;
  background-color: #707070;
  margin-right: 20px;

  @include respond(phone) {
  }
}
