@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeInVisible {
  from {
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
    visibility: visible;
  }
}

@keyframes rotate {
  100% {
    transform: rotate(1turn);
  }
}

@keyframes playAnimation {
  from {
    transform: scale(1.1);
  }
  to {
    transform: scale(0.9);
  }
}
@import "../../../utils/media.scss";
.slide {
  width: 238px;
  height: 134px;
  transition: all 300ms ease-in-out;
  position: relative;
  cursor: pointer;
  animation: fadeIn 1s ease-in-out;
  animation-fill-mode: backwards;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  @include respond(phone) {
    svg {
      display: none;
    }
    width: 224px;
    height: 94px;
  }
  &:hover {
    .title,
    .gradient {
      opacity: 1;
      visibility: visible;
    }
    .gradient {
      opacity: 0.9;
    }
  }
  img {
    width: 100%;
    object-fit: cover;
    // border: 2px solid transparent;
    transition: border 500ms ease;
    border-radius: 10px;
    &:hover {
      // border-color: #ffc404;
      @include respond(phone) {
        transform: none;
      }
    }
  }
}

.slideSearch {
  width: 238px;
  height: 134px;
  margin-bottom: 20px;
  margin-right: 20px;
  animation: fadeIn 1s ease-in-out;
  animation-fill-mode: backwards;
  cursor: pointer;
  position: relative;
  transition: all 300ms ease-in-out;

  @include respond(phone) {
    width: 169px;
    height: 94px;
    margin-right: 10px;
    max-width: 45%;
  }

  &:hover {
    transform: scale(1.1);
    z-index: 1000;
    @include respond(phone) {
      transform: none;
    }

    .title,
    .gradient {
      opacity: 1;
      visibility: visible;
    }
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    // border: 2px solid transparent;
    transition: border 300ms ease;
    border-radius: 10px;
    &:hover {
      // border-color: #ffc404;
    }
  }
}

.visible {
  opacity: 1;
}

.hidden {
  opacity: 0;

  // transform: none !important;
}

.title {
  transition: all 300ms ease-in-out;

  opacity: 0;
  visibility: hidden;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #ffffff;
  position: absolute;
  bottom: 22px;
  left: 22px;
  z-index: 1000;
  width: 180px;
  @include respond(phone) {
    width: 150px;
  }
}

.gradient {
  transition: all 300ms ease-in-out;

  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 100%);
  opacity: 0;
  visibility: hidden;
  // background-image: linear-gradient(to bottom, var(black) 49%, rgba(0, 0, 0, 0));
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  // border: 2px solid #ffc404;
  border-radius: 10px;
}

.translateFirst {
  &:hover {
    transform: scale(1.3) translate3d(1.5vw, 0, 0) !important;
    @include respond(phone) {
      transform: none !important;
    }
  }
}

.translateLast {
  &:hover {
    transform: scale(1.3) translate3d(-1.9vw, 0, 0) !important;
    @include respond(phone) {
      transform: none !important;
    }
  }
}

.svg {
  width: 100;
  height: 100%;
  position: absolute;
  top: 0;
  left: -10px;

  top: 50%;
  left: calc(50% - 3px);
  transform: translate(-50%, -50%);
  z-index: 10000;
}

.borderAnimation {
  //ANIM
  background: none;
  border: 0;
  // box-sizing: border-box;
  // margin: 1em;
  // padding: 1em 2em;

  // Using inset box-shadow instead of border for sizing simplicity
  box-shadow: inset 0 0 0 2px transparent;
  // Required, since we're setting absolute on pseudo-elements
  position: absolute;
  width: 98%;
  height: 100%;
  vertical-align: middle;
  border-radius: 10px;
  z-index: 100000;
  &::before,
  &::after {
    box-sizing: inherit;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 10px !important;
  }
}

.hoverSlide {
  width: 297px;
  height: 173px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10000;
  pointer-events: none;
}
